@import "./global.scss";
.app {
  height: 100vh;
  .sections {
    overflow: scroll;
    height: calc(100vh - 70px);
    background-color: pink;
    position: relative;
    top: 70px;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
    scrollbar-width: none;  //for firefox

    @include mobile{
      height: calc(100vh - 45px);
      top: 45px;
    }
    &::-webkit-scrollbar{
        display: none
    };

     > *{
      height: calc(100vh - 70px);
      scroll-snap-align: end;
      @include mobile{
        height: calc(100vh - 45px);
        top: 45px;
      }
    }
  }
}
